import React from 'react';
import {
	Grid,
} from '@material-ui/core';

import format from '../../../../configs/format';
import {
	Frame,
	TagInfo,
} from '@nvdunginest/emis-mui';

import { ContractDetailContext } from '../../stores/ContractDetailStore';
import CreateRequest from './CreateRequest';
import CancelRequest from './CancelRequest';
import CheckRequest from './CheckRequest';
import RejectRequest from './RejectRequest';
import ApproveRequest from './ApproveRequest';
import UpdateNetPrice from './UpdateNetPrice';
import Attachment from './Attachment';

export default function ContractHeader(): JSX.Element {
	const contract = React.useContext(ContractDetailContext).state.contract;

	return (
		<Frame title="Thng tin hp ng">
			<Grid container alignItems="stretch" spacing={1}>
				<TagInfo title="S hp ng" content={contract.contractNumber} />
				<TagInfo title="Loi hp ng" content={contract.contractType === 'D' ? "D n" : "Nguyn tc"} />
				<TagInfo title="D n" content={`${contract.projectCode}-${contract.projectName}`} />
				<TagInfo title="Nh cung cp" content={`${contract.vendorCode}-${contract.vendorName}`} />
				<TagInfo
					title="Gi tr hp ng"
					content={`${format.formatMoney(contract.contractValue)} VND`}
				/>
				<TagInfo title="iu khon thanh ton" content={contract.paymentTerm} />
				<TagInfo
					title="Ngy to hp ng"
					content={format.formatDate(new Date(contract.createdDate), 'dd/MM/yyyy')}
				/>
				<TagInfo title="Ngi to hp ng" content={`${contract.createdBy}-${contract.createdByFullName}`} />
				<Grid item xs={12} md={6} container spacing={1}>
					<CreateRequest />
					<CancelRequest />
					<CheckRequest />
					<RejectRequest />
					<ApproveRequest />
					<UpdateNetPrice />
					<Attachment />
				</Grid>
			</Grid>
		</Frame>
	)
}